import React from "react";
import Logo from "./assets/logo.png";
import {
  AiOutlineInbox,
  AiOutlinePhone,
  AiOutlineLogin,
  AiOutlineUserAdd,
  AiOutlineLink,
} from "react-icons/ai";
import { GoLocation } from "react-icons/go";

const Footer = () => {
  return (
    <div className="footerwrapper">
      <hr />
      <div className="cfooter">
        <div className="flogo">
          <img src={Logo} alt="logo" />
          <span>SnGlss</span>
        </div>
        <div className="block">
          <div className="detail">
            <span>contact us</span>
            <span className="pngLine">
              <GoLocation className="icon" />
              <span>lkk lkljl ;k;k 1122</span>
            </span>
            <span className="pngLine">
              <AiOutlinePhone className="icon" />
              <span>987456321</span>
            </span>
            <span className="pngLine">
              <AiOutlineInbox className="icon" />
              <span>shashanknathe@gmail.com</span>
            </span>
          </div>
        </div>
        <div className="block">
          <div className="detail">
            <span>Account</span>
            <span className="pngLine">
              <AiOutlineLogin className="icon" />
              <span>Signin</span>
            </span>
          </div>
        </div>
        <div className="block">
          <div className="detail">
            <span>Company</span>
            <span className="pngLine">
              <AiOutlineUserAdd className="icon" />
              <span>About us</span>
            </span>
          </div>
        </div>
        <div className="block">
          <div className="detail">
            <span>Resources</span>
            <span className="pngLine">
              <AiOutlineLink className="icon" />
              <span>Terms and conditions</span>
            </span>
          </div>
        </div>
      </div>
      <div className="copy">
        <span>&copy; copyright 2023 Shashank Nathe</span>
        <span>all rights reserved</span>
      </div>
    </div>
  );
};

export default Footer;
