import React, { useState } from "react";
import Plane from "./assets/hero1.png";
import { ProductsData } from "./data/products";

const Products = () => {
  const [menuProducts, setMenuProducts] = useState(ProductsData);
  const filter = (type) => {
    setMenuProducts(ProductsData.filter((product) => product.type === type));
  };
  return (
    <div className="pcontainer">
      <div className="phead">
        <img src={Plane} alt="head" />

        <h1>Our featured products</h1>
      </div>
      <div className="products">
        <ul className="pmenu">
          <li onClick={() => setMenuProducts(ProductsData)}>all</li>
          <li onClick={() => filter("kids")}>Kids</li>
          <li onClick={() => filter("sunglasses")}>Sunglasses</li>
          <li onClick={() => filter("computer")}>Computer glasses</li>
        </ul>

        <div className="plist">
          {menuProducts.map((product, i) => {
            return (
              <div className="product" key={i}>
                <div className="pleft-s">
                  <div className="pname">
                    <span>{product.detail}</span>
                    <span>{product.name}</span>
                  </div>
                  <span>{product.price}</span>
                  <div>Shop Now</div>
                </div>
                <img src={product.img} alt={product.name} className="pimg-p" />
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
};

export default Products;
